.container {
    background: linear-gradient(to right, #f79d00, #64f38c);
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.input {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
}

.input::placeholder {
    font: inherit;
    font-size: 18px;
}
.button {
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 0;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
}

.button:hover {
    opacity: 1;
}

.button-label {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    clip-path: inset(50%);
    border: 0;
}
.form {
    display: flex;
}